<template>
  <div class="playlist">
    <!-- 辅助路由{{this.$route.query.id}} -->
    <template v-if="songListDetall">
        <div class="header">
            <div class="mask" :style="{backgroundImage: `url(${songListDetall.coverImgUrl})`}"></div>
            <div class="bg-mask"></div>
            <div class="info-box">
                <div class="left">
                    <img :src="songListDetall.coverImgUrl" alt />
                    <span class="playcount">
                        <i class="fa fa-headphones"></i>
                        {{playCountParse(songListDetall.playCount)}}
                    </span>
                    <span class="icon">歌单</span>
                </div>
                <div class="right">
                    <h3>{{songListDetall.name}}</h3>
                    <div class="singer">
                        <img :src="songListDetall.creator.avatarUrl" alt />
                        <span class="text">{{songListDetall.creator.nickname}}</span>
                    </div>
                </div>
            </div>
        </div>

        <div class="info">
            <div class="tags">
                <span>标签：</span>
                <i v-for="(tag, index) in songListDetall.tags" :key="index">{{tag}}</i>
            </div>
            <div @click="showMoreDesc = !showMoreDesc">
                <div class="desc" :class="{show: showMoreDesc}">
                    <span>简介：</span>
                    <span>{{songListDetall.description}}</span>
                </div>
                <div class="more">
                    <i v-if="showMoreDesc" class="fa fa-angle-up"></i>
                    <i v-else class="fa fa-angle-down"></i>
                </div>
                </div>
            </div>

    </template>

    <template>
        <div class="songits">
            <div class="songits-box">
                <span>歌曲列表</span>
            </div>
            <div> 
                <SongItems v-for="(item, index) in songIts" :key="index" :songs="item" :options="{order: index}"  @translate-song="$emit('translate-song', $event)" :currentSong="currentSong"></SongItems>
            </div>
        </div>
    </template>
    
  </div>
</template>

<script>

import SongItems from '../components/SongItems'

export default {
    name: "PlayList",

    props: {
        currentSong: Object
    },

    components: {
        SongItems   
    },

    data() {
        return {
            songListId: this.$route.query.id,
            songListDetall: null,
            songIts: null,
            showMoreDesc: false
        };
    },

     methods: {
        getSongListData(id) {
            this.axios
                .get("https://music.kele8.cn/playlist/detail", {
                    params: {
                        id: id
                    }
                })
                .then(response => {
                    // 如果数据正确
                    // console.log(response.data.playlist);

                    this.songListDetall = response.data.playlist;
                    // console.log("this.songListDetall ==>", this.songListDetall);

                    this.songIts = response.data.playlist.tracks;
                    // console.log("this.songIts ==>", this.songIts);
                    

                    // 将数据放进本地存储
                    window.localStorage.setItem(
                        "sl" + this.$route.query.id,
                        JSON.stringify({
                            // 过期时间
                            expire: Date.now() + 3 * 60 * 60 * 1000,
                            result: response.data.playlist,
                            itsresult: response.data.playlist.tracks
                        })
                    );
                })
                .catch(err => {
                    console.log("err ==>", err);
                });

        /* this.axios({
                        method: 'GET',
                        url: 'https://music.kele8.cn//playlist/detail?id=24381616',
                        param: {
                            id: this.songListId
                        }
                    })
                    .then(response => {
                        // 如果数据正确
                        // console.log(response.data);
                        
                        this.songListDetall = response.data.playlist;
                        // console.log('this.songListId ==>', this.songListId);

                        // 将数据放进本地存储
                        window.localStorage.setItem('sl' + this.$route.query.id, JSON.stringify({
                            // 过期时间
                            expire: Date.now() + 3 * 60 * 60 * 1000,
                            result: response.data.playlist
                        }))
                        
                    }).catch(err => {
                        console.log('err ==>', err);
                    }) */
        },

        playCountParse(n) {
            if (n > 100000000) return (n / 100000000).toFixed(1) + "亿";

            if (n > 10000) return (n / 10000).toFixed(1) + "万";
        },

        routertoPlayListPage(id) {
            this.$router.push({ path: "playlist", query: { id: id } });
        }
    },

    created() {
        // this.getSongListData();

        const localSongListData = JSON.parse(
            window.localStorage.getItem("sl" + this.songListId)
        );

        if (localSongListData && localSongListData.expire > Date.now()) {
            // 存在且还没有过期
            this.songListDetall = localSongListData.result;
            this.songIts = localSongListData.itsresult;

        } else {
            // 已经过期
            this.getSongListData(this.songListId);
        }
    },

 /*    beforeRouteEnter(to, from, next) {

        const localSongListData = JSON.parse(
                window.localStorage.getItem("sl" + to.query.id)
        );

            if (localSongListData && localSongListData.expire > Date.now()) {
                // 存在且还没有过期
                // this.songListDetall = localSongListData.result;

                next(vm => {
                    // 通过 `vm` 访问组件实例
                    vm.songListId = Number(to.query.id);
                    vm.songListDetall = localSongListData.playlist;
                });
            } else {
                // 已经过期

                window.axios
                .get("https://music.kele8.cn/playlist/detail", {
                    params: {
                        id: to.query.id
                    }
                })
                .then(response => {
                    // 如果数据正确
                    let playlist = response.data.playlist;
                    console.log(playlist);

                    // 获取到的数据放入本地存储
                    window.localStorage.setItem(
                        "sl-" + to.query.id,
                        // 过期时间1小时
                        JSON.stringify({
                            expire: Date.now() + 3 * 60 * 60 * 1000,
                            playlist: response.data.playlist
                        })
                    );

                    // this.songListDetall = response.data.playlist;
                    // console.log("this.songListDetall ==>", this.songListDetall);

                    next(vm => {
                        vm.songListId = Number(to.query.id);
                        vm.songListDetall = playlist;
                    })

                })
                .catch(err => {
                    console.log("err ==>", err);
                });

            }   
        
        },  */

    /* // 路由守卫, 进入路由之前, 拦截路由
    beforeRouteUpdate(to, from, next) {
        // react to route changes...
        // don't forget to call next()
        // console.log(to, from);
        from;
        this.songListId = Number(to.query.id);

        next();
    }, */

    watch: {
        songListId() {
            console.log("变化了");
 
            const localSongListData = JSON.parse(
                window.localStorage.getItem("sl" + this.songListId)
            );

            if (localSongListData && localSongListData.expire > Date.now()) {
                // 存在且还没有过期
                this.songListDetall = localSongListData.result;
            } else {
                // 已经过期
                this.getSongListData(this.songListId);
            }
            
        }
    }
};
</script>

<style lang="less" scoped>
    .playlist {
        .header {
            position: relative;
            overflow: hidden;

            .mask {
            height: 180px;
            // background-image: url('https://p2.music.126.net/LwO8cNyConIE39KxC1Fdvw==/109951164940436245.jpg?imageView=1&type=webp&thumbnail=253x0');
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
            filter: blur(30px);
            }

            .bg-mask {
            position: absolute;
            width: 100%;
            height: 200px;
            top: 0;
            left: 0;
            background-color: rgba(0, 0, 0, 0.3);
            }

            .info-box {
            width: 90%;
            height: 126px;
            display: flex;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            // background-color: rosybrown;

            .left {
                width: 126px;
                height: 126px;
                position: relative;

                > img {
                width: 100%;
                display: block;
                }

                > .playcount {
                position: absolute;
                top: 3px;
                right: 5px;
                font-size: 12px;
                background-color: rgba(0, 0, 0, 0.3);
                color: #fff;
                }

                .icon {
                position: absolute;
                top: 10px;
                left: 0;
                font-size: 9px;
                color: #fff;
                background-color: #e4393c;
                width: 35px;
                height: 16px;
                text-align: center;
                line-height: 16px;
                border-radius: 0 17px 17px 0;
                // padding: 3px 8px;
                }
            }

            .right {
                width: calc(90% - 126px);
                margin-left: 20px;

                h3 {
                font-size: 17px;
                color: #fff;
                margin: 0;
                font-weight: normal;
                }

                .singer {
                margin-top: 20px;
                position: relative;
                > img {
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                    display: inline-block;
                    padding-right: 10px;
                }

                .text {
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: 34px;
                    margin: auto 0;
                    line-height: 30px;
                    height: 30px;
                    font-size: 14px;
                    color: #fff;
                }
                }
            }
            }
        }

        .info {
            background-color: #f8f8f8;
            font-size: 14px;
            padding: 15px 10px;

            .tags {
            margin-bottom: 15px;
            height: 20px;
            > i {
                font-size: 12px;
                line-height: 20px;
                padding: 1px 5px;
                font-style: normal;
                border: 1px solid #ddd;
                border-radius: 20px;
                margin-right: 10px;
            }
            }

            .desc {
                height: 4.0em;
                overflow: hidden;
                text-overflow: ellipsis;
                &.show {
                    height: auto;
                }
                span {
                    white-space: pre-wrap;
                    vertical-align: baseline;
                }
            }

            .more {
                overflow: hidden;

                i {
                    float: right;
                    font-size: 16px;
                }
            }
        }
        
        .songits {
            background-color: #f8f8f8;

            .songits-box {
                font-size: 12px;
                background-color: #eeeff0;
                padding: 5px 0;
                >span {
                    padding-left: 20px;
                }
            }
        }
    }
</style>